.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Navigator-Div {
  overflow: hidden;
  background-color: #333;
  position: fixed; /* Make it stick to the top */
  top: 0;
  width: 100%;
}

.Page-Header {
  align-self: center;
  align-content: center;
  color: gainsboro;
}

.Navigator-Div a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.Navigator-Div a:hover {
  border-style: solid;
  border-width: 1px;
  border-radius: 15px;
  border-color: #3939BF;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
